main {
  position: relative; }

.unselectable {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Chrome/Safari/Opera */
  -khtml-user-select: none;
  /* Konqueror */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
                                    not supported by any browser */ }

.container {
  padding: 0px 5%;
  width: 100%; }

.main_container {
  max-width: 1400px;
  margin: 0 auto; }

.upper_text {
  text-transform: uppercase; }

.absolute_center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

.sub_title {
  font-stretch: normal;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 400; }
  .sub_title_0 {
    font-size: 14px; }
  .sub_title_1 {
    font-size: 16px; }
  .sub_title_2 {
    font-size: 18px; }

.medium_title {
  font-stretch: 500;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 600; }
  .medium_title_2 {
    font-size: 16px; }
  .medium_title_3 {
    font-size: 20px; }
  .medium_title_4 {
    font-size: 24px; }

.standart_title {
  font-stretch: 600;
  font-style: normal;
  letter-spacing: normal;
  font-weight: 600; }
  .standart_title_2 {
    font-size: 16px; }
  .standart_title_3 {
    font-size: 18px; }
  .standart_title_4 {
    font-size: 24px; }
  .standart_title_5 {
    font-size: 32px; }

.main__title {
  font-weight: 700;
  font-stretch: normal;
  font-style: normal; }
  .main__title_5 {
    font-size: 32px; }

.bigger_title {
  font-weight: 900;
  font-stretch: normal;
  font-style: normal; }

.color_red {
  color: #E3000F; }

.color_red_2 {
  color: #F5222D; }

.color_green {
  color: #00eb8b; }

.color_black {
  color: #637280; }

.color_white {
  color: #fff; }

@media (max-width: 672px) {
  .main__title_7 {
    font-size: 40px; }
  .bigger_title_3 {
    font-size: 15px; } }

@media (max-width: 520px) {
  .main__title_2 {
    font-size: 20px; } }

@media (max-width: 410px) {
  .main__title_4 {
    font-size: 26px; } }

@media (max-width: 380px) {
  .main__title_7 {
    font-size: 29px; }
  .standart_title_1 {
    font-size: 15px; }
  .standart_title_5 {
    font-size: 23px; }
  .standart_title_4 {
    font-size: 20px; } }
