.status_messenger_section {
  position: relative;
  padding: 10px 0px;
  max-width: 800px;
  margin: 0 auto; }

.status_messenger_profile {
  margin: 20px 0;
  border-radius: 4px;
  padding: 20px;
  border: 1px solid #8DA2B5; }

.status_messenger_top_info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 15px; }

.status_messenger_btn {
  position: relative;
  cursor: pointer;
  border: 1px solid #8DA2B5;
  width: 16px;
  height: 16px;
  margin-right: 15px; }
  .status_messenger_btn:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80%;
    height: 1px;
    background: #8DA2B5;
    transform: translate(-50%, -50%); }
  .status_messenger_btn:before {
    transition: all .2s;
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1px;
    height: 80%;
    background: #8DA2B5;
    transform: translate(-50%, -50%); }

.status_messenger_title__block {
  display: flex;
  align-items: center; }

.status_messenger_hint {
  transition: all .2s;
  position: absolute;
  top: -40px;
  left: 0%;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-50%);
  padding: 5px 10px;
  border-radius: 4px;
  background: #637280;
  color: #fff; }
  .status_messenger_hint:before {
    content: '';
    position: absolute;
    bottom: -80%;
    left: 50%;
    transform: translateX(-50%);
    border: 20px solid transparent;
    border-top: 20px solid #637280;
    z-index: -1; }

.status_messenger_question {
  position: relative;
  margin-left: 10px; }
  .status_messenger_question svg {
    transition: all .2s;
    cursor: pointer;
    color: #637280;
    display: block;
    width: 15px;
    height: 15px;
    transform: scale(1); }
  .status_messenger_question:hover svg {
    transform: scale(1.1); }
  .status_messenger_question:hover .status_messenger_hint {
    opacity: 1;
    visibility: visible;
    left: 50%; }

.status_messenger_information__block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  opacity: .8;
  padding-top: 15px; }

.status_messenger_information_line {
  width: 30%;
  height: 1px;
  background: #637280;
  opacity: .8; }

.status_messenger_uptime {
  min-width: 150px;
  text-align: center;
  padding: 0px 10px; }

.status_messenger_date, .status_messenger_day {
  padding: 0px 10px;
  text-align: center;
  opacity: 0.6;
  min-width: 150px; }

.status_messenger_indicator__block {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%; }

.status_messenger_indicator {
  transition: all .2s;
  cursor: pointer;
  border-right: solid 2px #fff;
  border-left: solid 2px #fff;
  height: 34px;
  width: 3px;
  opacity: 0.7; }
  .status_messenger_indicator:hover {
    opacity: 1; }
  .status_messenger_indicator.animation {
    opacity: 1; }

.status_messenger_popup__block {
  transition: all .2s;
  position: absolute;
  top: 52px;
  visibility: hidden;
  opacity: 0;
  left: 0;
  min-width: 380px;
  z-index: 10;
  border: 1px solid #E0E0E0;
  border-radius: 4px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.15);
  background: #fff;
  padding: 15px; }
  .status_messenger_popup__block:after {
    content: '';
    position: absolute;
    top: -19px;
    left: 0;
    width: 100%;
    height: 30px;
    background: transparent;
    z-index: 10; }
  .status_messenger_popup__block_arrow {
    transition: all .2s;
    position: absolute;
    top: -13px;
    left: calc(50% + 2px);
    width: 25px;
    height: 25px;
    z-index: 1;
    border: 1px solid #E0E0E0;
    transform: translateX(-50%) rotate(45deg);
    background-color: #fff;
    border-right: 1px solid transparent;
    border-bottom: 1px solid transparent; }
  .status_messenger_popup__block_absolute_arrow {
    display: none;
    transition: all .2s;
    pointer-events: none;
    position: absolute;
    top: 40px;
    left: calc(50% + 4px);
    width: 25px;
    height: 25px;
    z-index: 9;
    border: 1px solid #E0E0E0;
    transform: translateX(-50%) rotate(45deg);
    background-color: #fff;
    border-right: 1px solid transparent;
    border-bottom: 1px solid transparent; }
    .status_messenger_popup__block_absolute_arrow.hiden {
      visibility: hidden;
      opacity: 0; }
  .status_messenger_popup__block.active {
    visibility: visible;
    opacity: 1; }
  .status_messenger_popup__block.good .status_messenger_popup_error__block {
    display: none; }
  .status_messenger_popup__block.normal .status_messenger_popup_error_bad {
    display: none; }
  .status_messenger_popup__block.bad .status_messenger_popup_error_normal {
    display: none; }

.status_messenger_popup_error__block {
  display: flex;
  align-items: center;
  padding: 10px;
  background: whitesmoke;
  border-radius: 4px;
  margin: 10px 0; }

.status_messenger_popup_error_svg {
  position: relative;
  width: 15px;
  height: 15px;
  margin-right: 15px; }
  .status_messenger_popup_error_svg img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

.status_messenger_popup_error_hours__block, .status_messenger_popup_error_minutes__block {
  margin-left: 15px;
  min-width: 50px; }

.status_messenger_popup_error_title {
  min-width: 130px; }

.status_messenger_popup_title {
  text-transform: uppercase; }

.status_messenger_profile__wrapper {
  display: none; }

.status_messenger_optional {
  border: 1px solid #8da2b5;
  border-radius: 4px;
  padding: 20px; }
  .status_messenger_optional.show .status_messenger_profile__wrapper {
    display: block; }
  .status_messenger_optional.show .status_messenger_btn:before {
    height: 0%; }
  .status_messenger_optional .status_messenger_profile__wrapper {
    margin-top: 25px; }
    .status_messenger_optional .status_messenger_profile__wrapper .status_messenger_profile {
      padding: 0px 20px; }
  .status_messenger_optional .status_messenger_profile {
    margin: 0;
    border: none;
    padding: 0; }

.popup_error_link {
  transition: all .2s;
  display: block;
  margin: 5px 0;
  padding-bottom: 3px;
  border-bottom: 1px solid transparent;
  width: -moz-fit-content;
  width: fit-content; }
  .popup_error_link:hover {
    border-bottom: 1px solid #637280; }

.status_content {
  margin-top: 60px;
  padding-bottom: 20px; }
  .status_content_title {
    padding: 20px; }
  .status_content_about__block {
    padding: 50px 20px; }
  .status_content_about_text {
    padding: 15px 0; }

.incidens__block {
  padding: 35px 0; }

.incidens_title {
  padding-bottom: 10px; }

.incidens_profile {
  margin-bottom: 30px;
  width: 100%; }
  .incidens_profile_date {
    padding-bottom: 10px;
    margin-bottom: 15px;
    border-bottom: 1px solid #637280; }
  .incidens_profile_warning {
    display: block;
    margin: 10px 0; }

.form_input {
  outline: none;
  display: block;
  width: 100%;
  border: 1px solid #637280;
  margin: 0 0 20px;
  padding: 10px 15px;
  box-sizing: border-box; }
  .form_input:focus {
    border: 1px solid #238ceb; }

.comment_title {
  padding-bottom: 10px; }

.comment__block {
  max-width: 800px;
  margin: 0 auto; }

@media (max-width: 700px) {
  .status_messenger_information_line {
    display: none; }
  .status_messenger_date, .status_messenger_day {
    min-width: unset; }
  .status_messenger_uptime {
    min-width: unset; } }

@media (max-width: 535px) {
  .header_bottom__block {
    flex-direction: column; }
  .container {
    padding: 0px 10px;
    width: 100%; }
  .header_logo_name {
    padding-top: 10px; }
  .header_title {
    padding-bottom: 10px; }
  .header_top__block {
    padding: 20px 20px;
    flex-direction: column; } }

@media (max-width: 490px) {
  .status_messenger_popup__block {
    min-width: unset;
    width: 100% !important;
    left: 0 !important; }
  .status_messenger_popup_error__block {
    flex-direction: column;
    align-items: flex-start; }
  .status_messenger_popup_error_hours__block, .status_messenger_popup_error_minutes__block {
    margin: 0; }
  .status_messenger_optional {
    padding: 10px; }
  .status_messenger_profile {
    padding: 10px; }
  .status_messenger_optional .status_messenger_profile__wrapper .status_messenger_profile {
    padding: 0; } }

@media (max-width: 400px) {
  .status_messenger_top_info {
    flex-direction: column; } }

@media (max-width: 380px) {
  .status_messenger_hint {
    display: none; }
  .status_messenger_question:hover .status_messenger_hint {
    opacity: 1;
    visibility: visible;
    left: unset;
    transform: unset;
    right: 0;
    top: -60px;
    display: block; }
  .status_messenger_hint::before {
    left: calc(100% - 40px);
    transform: unset; } }
