.header_logo_name {
  text-transform: uppercase; }

.header_top__block {
  padding: 100px 20px;
  display: flex;
  align-items: center; }

.header_img {
  width: 50px;
  height: 50px;
  margin-right: 30px; }
  .header_img img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.header_bottom__block {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between; }

.standart_btn {
  transition: all .2s;
  display: block;
  cursor: pointer;
  width: fit-content;
  letter-spacing: normal;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: 2px;
  padding: 10px 25px;
  min-width: 170px;
  text-align: center;
  border-radius: 4px;
  color: #fff;
  opacity: 0.8;
  border: solid 1px transparent;
  box-shadow: 0px 10px 30px rgba(56, 29, 219, 0.2); }
  .standart_btn:hover {
    opacity: 1; }
